.loginBox {
    position: fixed;
    top: 160px;
    right: 268px;
    
    width: 424px;
    height: 549px;
    border-radius: 46px;
    border-width: 1px;
    
    background: #8080804D;
    background-blend-mode: luminosity;
  
    border: 1px solid;
    border-image-source: linear-gradient(
      156.52deg, 
      rgba(255, 255, 255, 0.4) 2.12%, 
      rgba(255, 255, 255, 0.0001) 39%, 
      rgba(255, 255, 255, 0.0001) 54.33%, 
      rgba(255, 255, 255, 0.1) 93.02%
    );
  
    backdrop-filter: blur(100px);
  }

  .sign {
    position: absolute;
    top: 28.5px;
    right: -65px;
    
    width: 562px;
    height: 35px;
    
    font-family: 'SF Pro', sans-serif;
    font-weight: 700;
    font-size: 29px;
    text-align: center;
    color: white;
}

.uborder {
  all: unset;
  position: absolute;
  top: 454px;
  right: 109px;

  width: 206px;
  height: 54px;
  border-radius: 100px;
  
  background: rgba(255, 255, 255, 0.07);
  background-blend-mode: color-dodge;
  background: rgba(216, 210, 210, 0.16);

  font-family: 'SF Pro', sans-serif;
  font-weight: 540;
  font-size: 19px;
  line-height: 20px;
  letter-spacing: 0px;
  vertical-align: middle;
  color: white;
  text-align: center;

  cursor: pointer;
}

