.email {
  all: unset;
  position: absolute;
  top: 119px;
  right: 59.5px;

  width: 305px;
  height: 44px;
  padding-right: 20px;
  padding-left: 20px;
  border-radius: 12px;

  background: rgba(208, 208, 208, 0.5);
  background-blend-mode: luminosity;
  background: rgba(0, 0, 0, 0.1);

  box-shadow: 1px 1.5px 4px 0px rgba(0, 0, 0, 0.1) inset;
  box-shadow: 1px 1.5px 4px 0px rgba(0, 0, 0, 0.08) inset;
  box-shadow: 0px -0.5px 1px 0px rgba(255, 255, 255, 0.25) inset;
  box-shadow: 0px -0.5px 1px 0px rgba(255, 255, 255, 0.3) inset;

  color: white;

  box-sizing: border-box;
}

.email::placeholder { 
  color: #8BB9E0;
  opacity: 1; /* Полностью видимый цвет */
}


