/* src/Styles/PostCard.css */

.post-card {
    background-color: rgba(255, 255, 255, 0.1);
    color: white;
    border-radius: 16px;
    padding: 30px; /* Увеличиваем отступы для большего размера */
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    height: auto; /* Устанавливаем динамическую высоту */
  }
  
  .post-card:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 25px rgba(0, 0, 0, 0.4);
  }
  
  .post-header {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 20px;
  }
  
  .post-header img {
    border-radius: 50%;
    width: 50px;
    height: 50px;
  }
  
  .post-author {
    font-weight: bold;
    font-size: 1.3em; /* Увеличиваем размер шрифта */
  }
  
  .post-date {
    font-size: 1.1em;
    color: #bbb;
  }
  
  .post-content {
    font-size: 1.2em; /* Увеличиваем размер шрифта */
    line-height: 1.8; /* Увеличиваем межстрочный интервал */
  }
  
  .post-actions {
    display: flex;
    gap: 15px;
    margin-top: 15px;
  }
  
  .post-actions svg {
    cursor: pointer;
    transition: color 0.3s ease;
  }
  
  .post-actions svg:hover {
    color: #1da1f2; /* Синий цвет для активных иконок */
  }
  
  .post-actions .like-icon,
  .post-actions .comment-icon,
  .post-actions .share-icon {
    width: 20px;
    height: 20px;
  }
  /* Стили для новых кнопок */
.post-actions-buttons {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.edit-btn,
.delete-btn {
  background-color: transparent;
  border: 1px solid #ccc;
  color: #ccc;
  padding: 5px 10px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1em;
  transition: all 0.3s ease;
}

.edit-btn:hover,
.delete-btn:hover {
  background-color: #1da1f2;
  color: white;
  border-color: #1da1f2;
}
