/* Comments.css */

/* Стили для контейнера комментариев */
.comments-section {
    margin-top: 20px;
  }
  
  /* Стили для каждого комментария */
  .comment {
    border: 2px solid #ccc; /* Рамка */
    padding: 10px; /* Отступы внутри рамки */
    margin: 10px 0; /* Отступы между комментариями */
    width: 500px; /* Фиксированная ширина */
    max-width: 100%; /* Максимальная ширина для адаптивности */
    box-sizing: border-box; /* Учитываем padding и border в ширине */
    border-radius: 5px; /* Округлые углы */
    background-color: #f9f9f9; /* Цвет фона */
  }
  
  /* Стили для авторов комментариев */
  .comment-author {
    display: flex; /* Располагаем имя пользователя и кнопки в одну строку */
    justify-content: space-between; /* Распределяем элементы с отступами */
    font-weight: bold;
    margin-bottom: 5px;
    font-size: 14px;
    color: #0056b3;
    width: 100%; /* Ширина должна быть достаточной для размещения текста и кнопок */
  }
  
  /* Стили для содержимого комментария */
  .comment-content {
    margin-bottom: 10px;
    font-size: 14px;
    color: #333;
  }
  
  /* Стили для даты комментария */
  .comment-date {
    font-size: 12px;
    color: #888;
  }
  
  /* Стили для кнопки добавления комментария */
  .add-comment textarea {
    width: 100%;
    height: 100px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    resize: none;
    font-size: 14px;
  }
  
  .add-comment button {
    padding: 10px 15px;
    margin-top: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .add-comment button:hover {
    background-color: #0056b3;
  }
  
  /* Стили для блока с кнопками редактирования и удаления */
  .comment-actions {
    display: flex;
    gap: 10px; /* Отступы между кнопками */
  }
  
  /* Стили для кнопок редактирования и удаления */
  .comment-actions button {
    padding: 5px 10px;
    font-size: 12px; /* Устанавливаем размер шрифта такой же, как у имени пользователя */
    cursor: pointer;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #acadb3;
    display: inline-flex;
    align-items: center;
  }
  
  .comment-actions button:hover {
    background-color: #007bff;
    color: #fff;
  }
  