/* src/Styles/Main.css */

.background-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  background-image: url("../Images/bg.png");
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(40px);
  -webkit-backdrop-filter: blur(8px);
  z-index: 1;
}

.nav {
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  font-family: "SF Pro", sans-serif;
  font-weight: 700;
  font-size: 28px;
  text-align: center;
  color: white;
  z-index: 2;
}

.posts-container {
  z-index: 2;
  position: absolute;
  top: 100px; 
  left: 520px;/* Сдвигаем контейнер немного влево для оптимизации */
  width: 50%; /* Увеличиваем ширину контейнера с постами */
  max-height: calc(100vh - 150px); /* Ограничиваем по высоте, чтобы скролл не мешал */
  
  display: flex;
  flex-direction: column;
  gap: 20px; /* Увеличиваем промежуток между постами */
  align-items: flex-start; /* Выравниваем посты по левому краю */

  overflow-y: scroll;
  overflow-x: hidden;
}

/* Общий контейнер скроллбара */
::-webkit-scrollbar {
  width: 8px; /* или height: 8px для горизонтального */
}

/* Трек (фон скроллбара) */
::-webkit-scrollbar-track {
  background: #f0f0f0;
  border-radius: 10px;
}

/* Ползунок */
::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
  border: 2px solid #f0f0f0; /* вокруг ползунка */
}

/* При наведении */
::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}


.post-card {
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  border-radius: 16px;
  padding: 30px; /* Увеличиваем отступы для поста */
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  width: 100%; /* Ширина постов будет адаптироваться по размеру контейнера */
}

.post-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.4);
}

.post-author {
  font-weight: bold;
  font-size: 1.3em; /* Увеличиваем размер шрифта для имени автора */
}

.post-date {
  font-size: 1.1em;
  color: #bbb;
  margin-bottom: 10px;
}

.post-content {
  font-size: 1.2em; /* Увеличиваем размер шрифта для контента */
  line-height: 1.8; /* Увеличиваем межстрочный интервал */
}

/* 🔽 Адаптивность */
@media (max-width: 1024px) {
  .posts-container {
    width: 50%; /* Уменьшаем ширину контейнера для экранов менее 1024px */
  }
}

@media (max-width: 768px) {
  .posts-container {
    width: 80%; /* Ширина контейнера для экранов менее 768px */
  }

  .nav {
    font-size: 24px;
  }

  .post-card {
    padding: 20px; /* Меньше padding на маленьких экранах */
  }
}

@media (max-width: 480px) {
  .posts-container {
    width: 95%; /* Почти весь экран для контейнера с постами на мобильных */
  }

  .nav {
    font-size: 20px; /* Уменьшаем размер шрифта для заголовка */
    top: 20px;
  }

  .post-card {
    padding: 16px; /* Меньше padding для постов на мобильных */
  }

  .post-author {
    font-size: 1em;
  }

  .post-content {
    font-size: 1em;
  }
}
/* Фиксированный скроллбар */
.fixed-scrollbar {
  position: fixed;
  top: 0;
  right: 0;
  width: 8px;
  height: 100%;
  background-color: rgba(249, 243, 243, 0.97);
  z-index: 999;
  border-radius: 10px;
}

