.container {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url("../Images/bg.png");
    background-size: cover;
    background-position: center;
    position: relative;
    overflow: hidden; 
  }
  
  .container::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3); 
    z-index: 1; 
  }
  
  
  .container * {
    position: relative;
    z-index: 2;
  }
    
    .text {
