.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .modal {
    background: white;
    border-radius: 16px;
    width: 500px;
    max-width: 90%;
    padding: 20px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
    position: relative;
    z-index: 1001;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .cancelButton {
    background: none;
    border: none;
    color: #007aff;
    font-size: 16px;
    cursor: pointer;
  }
  
  .body {
    display: flex;
    gap: 12px;
  }
  
  .avatar {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .inputSection {
    flex: 1;
  }
  
  .username {
    font-weight: 500;
    margin-bottom: 4px;
    font-size: 14px;
    color: #444;
  }
  
  .username span {
    color: gray;
    font-weight: normal;
    cursor: pointer;
  }
  
  .textarea {
    width: 100%;
    min-height: 80px;
    resize: none;
    border: none;
    outline: none;
    font-size: 16px;
    margin-top: 6px;
    margin-bottom: 12px;
  }
  
  .tools {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 12px;
  }
  
  .tools img {
    width: 22px;
    height: 22px;
    cursor: pointer;
    opacity: 0.8;
  }
  
  .publishButton {
    background-color: #007aff;
    color: white;
    border: none;
    padding: 10px 18px;
    border-radius: 20px;
    cursor: pointer;
    font-weight: bold;
    float: right;
    transition: background-color 0.3s ease;
  }
  
  .publishButton:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  