.sidebar {
    position: absolute;
    top: 0;
    left: 0;
    width: 320px;
    height: 1120px;
    background: rgba(0, 0, 0, 0.15); /* последний указанный цвет */
    background-blend-mode: luminosity;
    box-shadow: 0.5px 0px 0px 0px rgba(84, 84, 88, 0.65);
    backdrop-filter: blur(10px); /* если хочешь стеклянный эффект */
    -webkit-backdrop-filter: blur(10px); /* поддержка для Safari */
    z-index: 2;
  }

.title {
  position: absolute;
  top: 28.5px;
  left: 28px;
  width: 272px;
  height: 35px;
  font-family: 'SF Pro', sans-serif;
  font-weight: 700;
  font-size: 29px;
  line-height: 100%;
  letter-spacing: 0px;
  color: white;
}

.logout {
  position: absolute;
  top: 24px;
  left: 1430px;
  width: 44px;
  height: 44px;
  gap: 8px;
  border-radius: 50px;

  background: rgba(255, 255, 255, 0.07);
  background-blend-mode: color-dodge;
  background: rgba(216, 210, 210, 0.16);
}

